<template lang="pug">
#app-password-generator
  h1 Генератор паролей онлайн
  .gen
    form
      .param-slider
        label Длина пароля
        .slider
          vue-slider(v-model="gen.length" :min=4 :max=30)
        span {{gen.length}}
      .param-checkbox.stringAllIncludes
        label.checkbox Допустимые символы
        input(type="text" v-model="gen.characters")
      .param-checkbox
        label.checkbox Числа
        toggle-button(v-model="gen.numbers" color="#9cd5ff")
        span например 12345
      .param-checkbox
        label.checkbox Буквы нижнего регистра
        toggle-button(v-model="gen.az" color="#9cd5ff")
        span например abcde
      .param-checkbox
        label.checkbox Буквы верхнего регистра
        toggle-button(v-model="gen.AZ" color="#9cd5ff")
        span например ABСDE
      .param-checkbox
        label.checkbox Символы
        toggle-button(v-model="gen.symbols" color="#9cd5ff")
        span например %!$#@
      .param-checkbox
        label.checkbox Дополнительные символы
        toggle-button(v-model="gen.additionalSymbols" color="#9cd5ff")
        span например {.>:|)
      .param-checkbox
        label.checkbox Сохранять настройки
        toggle-button(v-model="gen.saveSetting" color="#9cd5ff" @change="saveSetting")
        span приобновлении страницы или закрытии браузера
      .param-slider
        label Количество паролей
        .slider
          vue-slider(v-model="gen.passwordCount" :min=4 :max=30)
        span {{gen.passwordCount}}
  .btn-generate(@click="runGenerate(generateStart=true)")
    span.load(:class="{ rotating: generateStart}")
      <svg style="width:24px;height:24px" viewBox="0 0 24 24">
          <path fill="currentColor" d="M12,6V9L16,5L12,1V4A8,8 0 0,0 4,12C4,13.57 4.46,15.03 5.24,16.26L6.7,14.8C6.25,13.97 6,13 6,12A6,6 0 0,1 12,6M18.76,7.74L17.3,9.2C17.74,10.04 18,11 18,12A6,6 0 0,1 12,18V15L8,19L12,23V20A8,8 0 0,0 20,12C20,10.43 19.54,8.97 18.76,7.74Z" />
      </svg>
    span.text Генерировать
  .passwords-wrapper
    form
      .password-item(v-for="(pass, i) in passwords")
        input(type="text" :value="pass" :id="['val_', i].join('')")
        span.clipboard(@click="copyToClipboard(['val_', i].join(''))")
          <svg style="width:24px;height:24px" viewBox="0 0 24 24">
              <path fill="white" d="M19,21H8V7H19M19,5H8A2,2 0 0,0 6,7V21A2,2 0 0,0 8,23H19A2,2 0 0,0 21,21V7A2,2 0 0,0 19,5M16,1H4A2,2 0 0,0 2,3V17H4V3H16V1Z" />
          </svg>

</template>

<script>
import { ToggleButton } from 'vue-js-toggle-button'
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'


const defaultGen = {
        length: 15,
        characters: '',
        numbers: true,
        az: true,
        AZ: false,
        symbols: true,
        additionalSymbols: false,
        passwordCount: 10,
        saveSetting: false
}
export default {
  name: "App",
  components: {
    ToggleButton,
    VueSlider
  },
  data() {
    return {
      gen: {
       
      },
      data: {
        numbers: 123456789,
        aZ: 'abcdefghijklmnopqrstuvwxyz',
        symbols: '%!$#@&?_-+',
        additionalSymbols: '{}<>*^:;|'
      },
      passwords: [

      ],
      stringAllInsludeSymbols: false,
      generateStart: false,
      ctxPassword: ''
    }
  },
  watch: {
    'gen': {
      deep: true,
      handler (){
        this.combineCharacters();
        if(this.stringAllInsludeSymbols.length==0){
            this.stringAllInsludeSymbols = '****'
        }
        this.gen.characters = this.stringAllInsludeSymbols

      }
    }
  },
  created (){
    this.gen = defaultGen
    /*let storage = localStorage.getItem('gen')
    if(storage){
      this.gen = JSON.parse(storage)
    }
    else{
      
    }*/
  },
  mounted() {

    this.combineCharacters();
    this.gen.characters = this.stringAllInsludeSymbols
    this.runGenerate()


  },
  methods: {
    runGenerate(){

      this.passwords = []
      for(let i = 0; i<this.gen.passwordCount; i++){
        this.generate()
        this.passwords.push(this.ctxPassword)
      }
      setTimeout(()=>{ 
        this.generateStart = false 

      }, 200)

    },
    generate (){
      this.ctxPassword = ''
      for(let i = 0; i<this.gen.length; i++){
        let rand = this.getRandomInt(0, this.stringAllInsludeSymbols.length)
        this.ctxPassword+=this.stringAllInsludeSymbols[rand]
      }     
    },
    combineCharacters(){
      this.stringAllInsludeSymbols = ''
      if(this.gen.numbers){
        this.stringAllInsludeSymbols +=this.data.numbers
      }
      if(this.gen.az){
        this.stringAllInsludeSymbols +=this.data.aZ
      }
      if(this.gen.AZ){
        this.stringAllInsludeSymbols += this.data.aZ.toUpperCase()
      }
      if(this.gen.symbols){
        this.stringAllInsludeSymbols +=this.data.symbols
      }
      if(this.gen.additionalSymbols){
        this.stringAllInsludeSymbols +=this.data.additionalSymbols
      }

    },
    copyToClipboard (val){
      let elem = document.querySelector('#'+val)
      navigator.clipboard.writeText(elem.value).then(function() {
        elem.style.color = 'orange'
        setTimeout(()=>{
          elem.style.color = 'black'
        },1000)
      })
    },
    saveSetting (){
      if(this.gen.saveSetting){
        localStorage.setItem('gen', JSON.stringify(this.gen))
      }
      else{
        localStorage.clear()
      }
    },
    getRandomInt(min, max){
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min)) + min;
    }

  }
}
</script>

<style lang="sass">


#app-password-generator
  font-size: 18px
  width: 1100px
  border-radius: 8px
  padding: 40px
  padding-top: 50px
  user-select: none
  background-color: white
  box-shadow: 3px 3px 7px rgba(0, 50, 100, 0.2)
  h1
    padding-bottom: 20px
    border-bottom: 3px solid #ededed

.stringAllIncludes input
  padding: 5px 20px
  outline: 0
  border: 1px solid  #9c9c9c
  border-radius: 8px
  width: 300px

.gen
  width: 80%
  margin: 0 auto
.param-slider
  padding: 10px
  margin-top: 40px
  display: flex
  align-items: center
  width: 100%
  label
    width: 230px
    display: block
    font-weight: 600
    text-align: right
    padding-right: 30px
  .slider
    width: 270px
  span 
    padding-left: 15px
    color: #9c9c9c
.param-checkbox
  display: flex
  align-items: center
  padding: 10px
  label.checkbox
    width: 230px
    display: block
    font-weight: 600
    text-align: right
    padding-right: 30px
  toggle-button
  span
    padding-left: 30px  
    color: gray
.btn-generate
  margin: 30px auto
  background-color: rgb(191, 203, 217)
  color: white
  border-radius: 8px
  width: 230px
  padding: 10px 20px
  cursor: pointer
  display: flex
  align-items: center
  justify-content: center
  transition: 0.5s
  span.text
    display: block
    font-size: 20px
    padding-left: 7px
  .load
    display: flex
    align-items: center
    transition: .5s
  &:hover
    background-color:  #5bc0de
    .load
      transform: rotate(45deg)

.passwords-wrapper
  margin: 0 auto
  width: 80%
  padding: 20px 0
  form
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    .password-item
      display: flex
      justify-content: center
      align-items: center
      margin: 10px
      width: 250px
      input
        height: 40px
        padding: 10px 20px
        transition: color 0.5s
        border-radius: 8px 0 0 8px
        outline: 0
        border: 1px solid  #9c9c9c
      .clipboard
        height: 40px
        width: 50px
        padding: 5px
        background-color: #5bc0de
        border: 1px solid  #46b8da
        cursor: pointer
        display: flex
        align-items: center
        justify-content: center
        border-radius: 0 8px 8px 0
        &:hover
          background-color: #46b8da
.vue-slider-dot-handle
  transform: scale(1.5)



@keyframes rotating
  from
    -ms-transform: rotate(0deg)
    -moz-transform: rotate(0deg)
    -webkit-transform: rotate(0deg)
    -o-transform: rotate(0deg)
    transform: rotate(0deg)

  to
    -ms-transform: rotate(360deg)
    -moz-transform: rotate(360deg)
    -webkit-transform: rotate(360deg)
    -o-transform: rotate(360deg)
    transform: rotate(360deg)

.rotating
  -webkit-animation: rotating 0.5s linear infinite
  -moz-animation: rotating 0.5s linear infinite
  -ms-animation: rotating 0.5s linear infinite
  -o-animation: rotating 0.5s linear infinite
  animation: rotating 0.5s linear infinite
</style>